<!-- 书本详情页 -->
<template>
  <div class="xsreader">
    <div class="xsreader_subject">
      <div class="xsreader_tips" v-if="isOk" @click="getshow">
        <p>点击呼出菜单</p>
      </div>
      <div class="xsreader_title">
        <h1>{{main.title}}</h1>
      </div>
      <!-- 文章内容 -->
      <div class="xsreader_content" ref="content" @touchstart="touchstart" v-if="isloading"></div>
      <div class=" load" ref="content" @touchstart="touchstart" v-else >
        <van-loading size="24px">加载中...</van-loading>
      </div>


      <!-- 广告位置 -->
      <div class="ad_fixed"></div>
      <div class="ad_box"></div>

      <div class="xsreader_flip">
        <div class="xsreader_flip_shang flip_fanye">
          <div class="flip_shang_img flip_img">
            <img src="../../assets/img/s.png" alt />
          </div>
          <div class="flip_shang_txt flip_txt" @click="hadelup">
            <span>上一章</span>
          </div>
        </div>
        <div class="xsreader_flip_mulu flip_fanye">
          <div class="flip_mulu_img">
            <img src="../../assets/img/reader_mulu.png" alt />
          </div>
          <router-link :to="{path:'mulu',query:{id:name_id}}">
            <div class="flip_mulu_txt">
              <span>目录</span>
            </div>
          </router-link>
        </div>
        <div class="xsreader_flip_xia flip_fanye">
          <div class="flip_xia_txt flip_txt" @click="hadeldown">
            <span>下一章</span>
          </div>
          <div class="flip_xia_img flip_img">
            <img src="../../assets/img/x.png" alt />
          </div>
        </div>
      </div>
    </div>

    <div class="xsreader_menu">
      <div class="xsreader_menu_top" v-if="isshow">
        <div class="top">
          <div class="back" @click="$router.back()">
            <van-icon name="arrow-left"  />
            <span>返回</span>
          </div>
          <span class="book">加入书架</span>
        </div>
      </div>

      <router-link :to="{path:'mulu',query:{id:name_id}}">
        <div class="xsreader_menu_middle" v-if="isshow">
          <div class="menu_middle_img">
            <img src="../../assets/img/left_ml.png" alt />
          </div>
          <div class="menu_middle_txt">
            <span>目录</span>
          </div>
        </div>
      </router-link>

      <div class="xsreader_menu_bottom" v-if="isshow">
        <div class="xsreader_menu_up menu_bottom" @touchstart="hadelup">
          <div class="menu_img_box">
            <img src="../../assets/img/reader_s.png" alt />
          </div>
          <div class="menu_up_txt menu_txt">
            <span>上一章</span>
          </div>
        </div>
        <div class="xsreader_menu_type menu_bottom">
          <div class="menu_pic_box">
            <img src="../../assets/img/zt.png" alt />
          </div>
          <div class="menu_type_txt menu_txt">
            <span>字体</span>
          </div>
        </div>
        <div class="xsreader_menu_mode menu_bottom">
          <div class="menu_pic_box">
            <img src="../../assets/img/yj.png" alt />
          </div>
          <div class="menu_mode_txt menu_txt" @click="handlecheck">
            <span>夜间</span>
          </div>
        </div>
        <div class="xsreader_menu_down menu_bottom" @touchstart="hadeldown">
          <div class="menu_img_box">
            <img src="../../assets/img/reader_x.png" alt />
          </div>
          <div class="menu_mode_txt menu_txt">
            <span>下一章</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "xsreader",
  data() {
    return {
      isOk: true,
      isshow: true,
      isloading:false,
      main: {},
      name_id: "",
      cat_id: "",
      isday: true
    };
  },
  components: {},

  methods: {
    getshow() {
      this.isOk = false;
      this.isshow = false;
      localStorage.setItem("mask", "");
    },
    getmain() {
      const id = this.$route.query.id;
      const name_id = this.$route.query.name;
      this.name_id = name_id;
      this.$axios({ url: `articles/${name_id}/${id}` }).then(res => {
        this.main = res.data.data;
        setTimeout(() => {
          this.$refs.content.innerHTML = this.main.content;
        },300);
      });
    },
    touchstart(e) {
      const Y = e.targetTouches[0].clientY;
      const X = e.targetTouches[0].clientX;
      if (Y > 10 && Y < 250 && X < 300) {
        this.isshow = !this.isshow;
      }
      localStorage.setItem("mask", "ok");
    },
    hadelup() {
      const cat_id = this.$route.query.id;
      setTimeout(() => {
        if (cat_id > 0) {
          this.$router.replace({
            path: "xsreader",
            query: {
              name: this.name_id,
              id: cat_id - 1
            }
          });
        }
      }, 500);
    },

    hadeldown() {
      const cat_id = this.$route.query.id;
      this.cat_id = cat_id;
      if (cat_id < 9999) {
        this.cat_id++;
        this.$router.replace({
          path: "xsreader",
          query: {
            name: this.name_id,
            id: this.cat_id
          }
        });
      }
    },
    handlecheck() {}
  },

  mounted() {
    const state = localStorage.getItem("mask");
    this.getmain();
    if (state) {
      this.isOk = false;
    }

    setTimeout(() => {
      this.isloading=true
    }, 500);
  },

  watch: {
    $route() {
      this.getmain();
    }
  }
};
</script>

<style lang="scss">
body,
html {
  width: 100%;
  height: 100%;
}
.xsreader {
  background-color: #fffae4;
  position: relative;
}
.active {
  display: none;
}

.xsreader_subject {
  .ad_box {
    width: 100vw;
    height: 35vw;
  }
  .ad_fixed {
    position: fixed;
    bottom: 12vw;
    width: 100vw;
    height: 30vw;
    background-color: #ec971f;
  }
  .xsreader_tips {
    position: absolute;
    width: 100vw;
    height: 100vh;
    z-index: 10000000;
    background-color: rgba($color: #000, $alpha: 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    p {
      letter-spacing: 3px;
      text-align: center;
      height: 10vw;
      width: 60vw;
      font-size: 7vw;
      color: #fff;
      font-weight: 500;
    }
  }
  .xsreader_title {
    width: 100%;
    height: 11vw;
    line-height: 11vw;
    text-align: center;
    font-size: 4vw;
    font-weight: bold;
  }
  .xsreader_content {
    width: 92vw;
    margin-left: 4vw;
    p {
      font-size: 32/750 * 100vw;
      line-height: 1.3em;
      text-indent: 2em;
    }
  }
  .load{
    position:absolute;
    top: 30vw;
    left: 40vw;
  }
  .xsreader_flip {
    height: 12vw;
    width: 92vw;
    margin: 0 4vw;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .flip_fanye {
      width: 28vw;
      height: 7.87vw;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 4px;
      .flip_img {
        width: 10px;
        height: 14px;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .flip_txt {
        width: 14vw;
        height: 3.5vw;
        display: flex;
        align-items: center;
        justify-content: center;
        span {
          font-size: 3.5vw;
        }
      }
    }
  }
  .xsreader_flip_shang {
    background-color: #fff;
  }
  .xsreader_flip_xia {
    background-color: #ff5f5c;
  }
  .xsreader_flip_mulu {
    background-color: #fff;
    .flip_mulu_img {
      width: 16px;
      height: 15px;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .flip_mulu_txt {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 10vw;
      height: 3.5vw;
      span {
        font-size: 3.5vw;
      }
    }
  }
}

.xsreader_menu {
  .xsreader_menu_top {
    width: 100%;
    height: 12vw;
    background-color: #333;
    position: fixed;
    display: flex;
    color: #fff;
    top: 0;
    left: 0;
    .top {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 2vw;
      .back{
        display: flex;
        flex: 1;
      }
      .book{
        flex: 1;
        text-align: right;
      }
     
     
    }
    .xsreader_menu_top_fh {
      position: absolute;
      left: 4vw;
      top: 4vw;
      width: 16vw;
      height: 8vw;
      color: #fff;
      .menu_top_fh_img {
        width: 3.2vw;
        height: 3.2vw;
        img {
          width: 100%;
          height: 100%;
        }
      }
      span {
        position: absolute;
        left: 4.2vw;
        top: 0vw;
        font-size: 4vw;
      }
    }
    .xsreader_menu_top_sj {
      position: absolute;
      right: 4vw;
      top: 4vw;
      height: 8vw;
      color: #fff;
      span {
        font-size: 4vw;
      }
    }
  }

  .xsreader_menu_middle {
    position: fixed;
    width: 12vw;
    height: 16vw;
    left: 0;
    top: 40vh;
    background-color: #333;
    border-bottom-right-radius: 8px;
    border-top-right-radius: 8px;
    .menu_middle_img {
      position: absolute;
      left: 2.6vw;
      top: 2.5vw;
      width: 7vw;
      height: 6.5vw;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .menu_middle_txt {
      position: absolute;
      left: 2vw;
      top: 10vw;
      text-align: center;
      width: 8vw;
      height: 5vw;
      overflow: hidden;
      span {
        font-size: 3.5vw;
        color: #fff;
      }
    }
  }

  .xsreader_menu_bottom {
    position: fixed;
    display: flex;
    align-items: center;
    bottom: 0;
    background-color: #333;
    width: 92vw;
    height: 17vw;
    text-align: center;
    padding: 0 4vw;
    .menu_bottom {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 23vw;
      height: 13vw;
      text-align: center;
    }
    .menu_img_box {
      width: 23vw;
      height: 7vw;
      overflow: hidden;
      img {
        width: 7vw;
        height: 7vw;
        color: #fff;
      }
    }
    .menu_txt {
      height: 6vw;
      line-height: 6vw;
      font-size: 4vw;
      color: #fff;
    }
    .menu_pic_box {
      width: 23vw;
      height: 7vw;
      overflow: hidden;
      img {
        width: 7.2vw;
        height: 100%;
        color: #fff;
      }
    }
  }
}
</style>
