<!-- 首页 -->
<template>
  <div class="Fpage">
    <mheader :info="webinfo" />
    <ad />
    <recommend :list="list" />
    <ad />
    <minassort :assortlist="assortlist" />
    <mfooter />
  </div>
</template>

<script>
// @ is an alias to /src
import mheader from "../components/mheader.vue";
import mfooter from "../components/mfooter.vue";
import ad from "../components/ad.vue";
import recommend from "./Fpage/recommend.vue";
import minassort from "./Fpage/minassort.vue";

export default {
  name: "Fpage",
  components: {
    mheader,
    mfooter,
    ad,
    recommend,
    minassort
  },
  data() {
    return {
      list: [],
      webinfo:{},
      assortlist:{}
    };
  },
  mounted() { 
    this.getpush();
    this.getassort()
   
  },

  methods: {
    getpush() {
      this.$axios({
        url: "/articles",
        params: {
          limit: 4,
          type: "push"
        }
      }).then(res => {
        const { data } = res.data;
		    this.list = data.push
      });
    },
    getassort(){
      this.$axios({
        url:"/articles?limit=10&category=1-2-3-4-5-6"}).then(res=>{
          this.assortlist=res.data.data
       
          
          })
    
    }


  
  }
};
</script>

<style lang="scss">
.Fpage {
  background-color: #f0f0f2;
}
</style>
