import Vue from 'vue'
import Router from 'vue-router'

import Fpage from "../views/Fpage.vue"
import Assort from "../views/Assort.vue"
import Ranking from "../views/Ranking.vue"
import Bookshelf from "../views/Bookshelf.vue"
import Xsreader from "../views/reader/xsreader.vue"
import Detail from '../views/detail.vue'
import Login from '../views/user/Login.vue'
import Register from '../views/user/register'
import Mulu from '../components/mulu'
import Xssearch from '../views/Xssearch'
import Success from '../views/user/success'
import Userinfo from '../views/user/userinfo'
import Editname from '../views/user/editname'














Vue.use(Router)

export default new Router({
	routes: [{
			path: '/',
			name: 'Fpage',
			component: Fpage
		},
		{
			path: '/Assort',
			name: 'Assort',
			component: Assort,
			meta: {
				pageTitle: '分类',
				keepAlive: true
			}
		},
		{
			path: '/Ranking',
			name: 'Ranking',
			component: Ranking
		},
		{
			path: '/Bookshelf',
			name: 'Bookshelf',
			component: Bookshelf
		},
		{
			path: "/xsreader",
			name: "Xsreader",
			component: Xsreader
		},
		{
			path: "/detail",
			name: "文章详情",
			component: Detail
		},
		{
			path: "/login",
			name: "登录",
			component: Login
		},
		{
			path: "/register",
			name: "注册",
			component: Register
		},
		{
			path: "/mulu",
			name: "目录",
			component: Mulu
		},

		{
			path: "/Xssearch",
			name: "搜索",
			component: Xssearch,
			meta: {
				pageTitle: '搜索',
				keepAlive: true
			}
			
		},

		{
			path: "/success",
			name: "注册成功",
			component: Success,
		},
		{
			path: "/userinfo",
			name: "用户中心",
			component: Userinfo,
		},
		{
			path: "/editname",
			name: "修改昵称",
			component: Editname,
		}

	],

	scrollBehavior(to, from, savedPosition) {
		if (savedPosition) {
			return
		} else {
			return {
				x: 0,
				y: 0
			}
		}
	}
})