<template>
  <div class="Xssearch">
    <top title="搜索"></top>
    <div class="Xssearch_content_box">
      <div class="Xssearch_content_top_box">
        <div class="Xssearch_content_inp_box">
          <input type="text" placeholder="请输入关键字" v-model="searchvalue" />
          <div class="Xssearch_search_kuang" @touchend="handlesearch">
            <div class="Xssearch_search_icon">
            <van-icon name="search" size="8vw"/>
            </div>
          </div>
        </div>
      </div>

      <!-- <div class="Xssearch_content_jilu">
        <div class="Xssearch_content_jilu_title_box">
          <div class="Xssearch_content_jilu_title">
            <div class="Xssearch_jilu_title_left">
              <span>搜索历史</span>
            </div>
            <div class="Xssearch_jilu_title_right" @click="handleclear">
              <span>清除记录</span>
            </div>
          </div>
        </div>

        <div class="Xssearch_content_jilu_message_box" v-for="(item,index) in his " :key="index">
          <span>{{item}}</span>
        </div>
      </div> -->

      <div class="recommend" v-if="searchvalue">
        <p class="title">搜索"{{searchvalue}}"的结果</p>
        <div
          class="rec_item"
          v-for="(item,index) in all"
          :key="index"
          @click="handlereader(item.id)"
        >
          <img :src="item.thumb" v-if="item.thumb" />
          <img src="../assets/img/min.png" v-else />

          <div class="info">
            <p style="font-size:3.5vw;">{{item.title}}</p>
            <p class="ft-col">作者：{{item.author}}</p>
            <p class="ft-col">分类：{{item.category}}</p>
            <span class="sj"></span>
          </div>
        </div>
      </div>

      <rankcontent></rankcontent>
    </div>
    <mfooter></mfooter>
  </div>
</template>

<script>
import Top from "../components/top.vue";
import mfooter from "../components/mfooter.vue";
import rankcontent from "./Ranking/rankcontent.vue";
import { coral } from "color-name";
export default {
  components: {
    Top,
    mfooter,
    rankcontent
  },
  data() {
    return {
      list: {},
      searchvalue: "",
      result: {},
      more: {},
      all: [],
      his: []
    };
  },
  methods: {
    handlesearch() {
      const hisdata = JSON.parse(localStorage.getItem("his") || "[]");
      hisdata.push(this.searchvalue);
      localStorage.setItem("his", JSON.stringify(hisdata));
      
      if (this.searchvalue !== "") {
        this.all = [];
        this.$axios({
          url: `/articles?limit=9999&keyword=${this.searchvalue}&category=1-2-3-4-5-6`
        }).then(res => {
          this.result = res.data.data;
          console.log(this.result);
          for (let key in this.result) {
            this.all = this.all.concat(this.result[key]);
          }
        });
      }
    },
    handlereader(id) {
      this.$router.push({
        path: "detail",
        query: {
          id
        }
      });
    },
    handleclear() {
      localStorage.setItem("his", "[]");
    }
  },

  mounted() {
    this.his = JSON.parse(localStorage.getItem("his"));
  }
};
</script>

<style lang="scss">
.Xssearch_content_box {
  .Xssearch_content_top_box {
    height: 23vw;
    display: flex;
    justify-content: center;
    align-items: center;
    .Xssearch_content_inp_box {
      width: 92vw;
      height: 7.733vw;
      display: flex;
      justify-content: center;
      align-content: center;
      input {
        width: 84.5vw;
        height: 7.733vw;
        padding-left: 3vw;
        font-size: 3.733vw;
        box-sizing: border-box;
        border: 1px solid #ddd;
        outline: none;
      }
      .Xssearch_search_kuang {
        width: 7.467vw;
        height: 7.733vw;
        background-color: #e73f3f;
        text-align: center;
        line-height: 7.733vw;
        i {
          color: #fff;
          font-size: 4.5vw;
        }
      }
    }
  }
  .Xssearch_content_jilu {
    border-top: 1px solid #ddd;
    .Xssearch_content_jilu_title_box {
      width: 100%;
      height: 10vw;
      .Xssearch_content_jilu_title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 8.5vw;
        margin: 0 4vw;
        .Xssearch_jilu_title_left {
          font-size: 4vw;
          color: #333;
        }
        .Xssearch_jilu_title_right {
          font-size: 3.2vw;
          color: #333;
        }
      }
    }
    .Xssearch_content_jilu_message_box {
      display: flex;
      justify-content: center;
      align-items: center;
      border-bottom: 1px solid #ddd;
      .Xssearch_content_jilu_message {
        width: 92vw;
        span {
          font-size: 3.733vw;
          color: #999;
        }
      }
    }
  }
  .recommend {
    height: 113vw;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    .title {
      line-height: 8.13vw;
      padding: 0 4vw;
      font-size: 4.27vw;
      border-bottom: 1px solid #ccc;
      background: #f0f0f2;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .rec_item {
      border-bottom: 1px solid #f0f0f2;
      padding: 2vw 0;
      display: flex;
      img {
        width: 15.47vw;
        height: 21.07vw;
        margin: 0 4vw;
      }
      p {
        font-size: 3.7vw;
        line-height: 6vw;
      }
      .num {
        font-size: 3.47vw;
        background-color: #f0f0f2;
      }
      .sj {
        float: left;
        border-width: 5px;
        margin-top: 3px;
        margin-left: -10px;
        border-style: solid;
        border-color: transparent #f0f0f2 transparent transparent;
      }
    }
  }
}
</style>
