<template>
  <div>
    <Top title="昵称修改" />
    <van-cell-group>
      <van-field v-model="value" placeholder="请输入用户名" right-icon="close" @click-right-icon="reset" />
    </van-cell-group>

    <div class="btn" @click="handleedit">
      <span>提交</span>
    </div>
  </div>
</template>

<script>
import Top from "../../components/top";
export default {
  components: {
    Top
  },
  data() {
    return {
      value: ""
    };
  },
  methods: {
    reset() {
      this.value = "";
    },

    handleedit() {
      const params = this.value;
      this.$axios({
        url: "user",
        method: "put",
        headers: {
          Authorization:
            "Bearer " +
            JSON.parse(localStorage.getItem("userinfo") || `{}`).api_token
        },
        data:{
          nickname:this.value
        }
      }).then(res=>{
         this.$toast.success("修改成功")
         const user = JSON.parse(localStorage.getItem("userinfo"));
         user.nickname=this.value
         localStorage.setItem("userinfo",JSON.stringify(user))

      })
    }
  },

  mounted() {
    
    const user = JSON.parse(localStorage.getItem("userinfo"));
    this.value = user.nickname;
    
    
  }
};
</script>

<style scoped lang="scss">
.btn {
  text-align: center;
  margin: 95/750 * 100vw;
  line-height: 10.67vw;
  background-color: #b33836;
  border-radius: 0.67vw;
  span {
    font-size: 3.73vw;
    letter-spacing: 0vw;
    color: #ffffff;
  }
}
</style>